/** @jsx jsx */
import { jsx, css } from "@emotion/react"

import { useItemsState } from "../../../items/_state/hooks"
import { usePortal } from "Hooks"
import { findPortalElement } from "Utils"
import { hasLink } from "Common/settings"
import { useCollectionState } from "../_state/hooks"

const Link = wp.element.lazy(() =>
  import(/* webpackChunkName: 'Link-public' */ "../../../link")
)

function TitleContent({ title }) {
  const CustomHeading = `h${shopwp.misc.isSingularCollections ? "1" : "2"}`

  const CollectionTitleCSS = css`
    margin-top: 0;
  `

  return (
    <CustomHeading
      itemProp="name"
      className="wps-collection-title"
      css={CollectionTitleCSS}
    >
      {title}
    </CustomHeading>
  )
}
function CollectionTitle() {
  const itemsState = useItemsState()
  const collectionState = useCollectionState()

  return usePortal(
    <div
      className="wps-component wps-component-collection-title"
      aria-label="Collection Title"
    >
      {hasLink(itemsState.settings) && !itemsState.settings.isSingular ? (
        <Link
          type="collections"
          payload={collectionState.payload}
          linkTo={itemsState.settings.linkTo}
          target={itemsState.settings.linkTarget}
        >
          <TitleContent title={collectionState.payload.title} />
        </Link>
      ) : (
        <TitleContent title={collectionState.payload.title} />
      )}
    </div>,
    findPortalElement(itemsState.settings.dropzoneCollectionTitle)
  )
}

export default CollectionTitle
